@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap);
/* home section */

.home-section {
  display: flex;
  padding: 100px 0;
}

.home-text,
.home-img {
  width: 100%;
  padding: 15px;
}

.home-text p {
  font-size: 1.8rem;
}

.home-text h1 {
  font-size: 4rem;
  text-transform: capitalize;
}

.home-text h2 {
  font-size: 2rem;
  font-weight: 300;
  margin: 0 0 30px;
  text-transform: capitalize;
}

.home-text .btn {
  margin: 0 15px 15px 0;
}

.home-img .img-box {
  background-color: var(--white-alpha-25);
  border: 8px solid var(--white-alpha-25);
  border-radius: 50%;
  margin: auto;
  max-width: 360px;
}

.home-img .img-box img {
  border-radius: 50%;
  width: 100%;
}

.section-title {
  margin-top: 20px;
}

.about-img {
  padding: 0 15px;
}

.about-text {
  padding: 35px 15px;
}

.about-img .img-box {
  background-color: var(--white-alpha-25);
  border: 1px solid var(--white-alpha-40);
  border-radius: 10px;
  margin: auto;
  max-width: 380px;
}

.about-img .img-box img {
  width: 100%;
}

.about-text h3 {
  font-size: 2rem;
  margin: 20px 0;
  text-transform: capitalize;
}

.about-text .skills {
  display: flex;
  flex-wrap: wrap;
}

.about-text .skill-item {
  background-color: var(--white-alpha-25);
  border: 1px solid var(--white-alpha-40);
  border-radius: 20px;
  margin: 0 10px 10px 0;
  padding: 5px 15px;
  text-transform: capitalize;
}

.about-tabs {
  align-items: center;
  justify-content: center;
  display: flex;
  margin-top: 20px;
}

.about-tabs .tab-item {
  background-color: transparent;
  border: none;
  color: var(--blue-dark);
  cursor: pointer;
  display: inline-block;
  font-size: 2rem;
  font-weight: 500;
  margin: 0 30px 0 0;
  opacity: 0.5;
  padding: 2px 0;
  position: relative;
  text-transform: capitalize;
  transition: all 0.3s ease;
}

.about-tabs .tab-item:last-child {
  margin: 0;
}

.about-tabs .tab-item::before {
  background-color: var(--blue-dark);
  bottom: 0;
  content: "";
  height: 1px;
  left: 0;
  position: absolute;
  transition: width 0.3s ease;
  width: 0%;
}

.about-tabs .tab-item:focus {
  border-right: 1px solid var(--main-color);
  border-bottom: 1px solid var(--main-color);
  box-shadow: 0 1px 3px 0 var(--main-color);
  padding: 3px;
}
.about-tabs .tab-item:hover {
  border-right: 1px solid var(--main-color);
  border-bottom: 1px solid var(--main-color);
  box-shadow: 0 1px 3px 0 var(--main-color);
  padding: 3px;
}

.about-text .timeline {
  position: relative;
}

.about-text .timeline::before {
  background-color: var(--main-color);
  content: "";
  height: 100%;
  left: 5px;
  position: absolute;
  top: 0;
  width: 1px;
}

.about-text .tab-content {
  padding: 40px 0;
}

.about-text .timeline-item {
  margin-bottom: 30px;
  padding: 10px 0 0 30px;
  position: relative;
}

.about-text .timeline-item::before {
  background-color: var(--main-color);
  border-radius: 50%;
  content: "";
  height: 11px;
  left: 0;
  position: absolute;
  top: 16px;
  width: 11px;
}

.about-text .timeline-item:last-child {
  margin-bottom: 0;
}
.about-text .timeline-item .date {
  color: var(--main-color);
  display: block;
  font-weight: 400;
  margin: 0 0 10px;
}
.about-text .timeline-item h4 {
  font-size: 1.8rem;
  margin: 0 0 10px;
  text-transform: capitalize;
}
.about-text .timeline-item h4 span {
  font-weight: 400;
}

.about-text .btn {
  margin: 0 15px 15px 0;
}

.portfolio-section h2 {
  padding-top: 40px;
  padding-bottom: 30px;
}
.portfolio-item {
  margin: 0 15px 30px;
}
.portfolio-item-thumbnail {
  background-color: var(--white-alpha-25);
  border: 1px solid var(--white-alpha-40);
  border-radius: 10px;
  padding: 10px;
}
.portfolio-item-thumbnail img {
  border-radius: 10px;
  width: 100%;
}

.portfolio-item h3 {
  font-size: 2rem;
  margin: 20px 0;
  text-transform: capitalize;
}

.portfolio-items-details {
  display: none;
}

/* popup */
.portfolio-popup {
  height: 100%;
  left: 0;
  overflow-y: auto;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 200;
}

.pp-inner {
  align-items: center;
  display: flex;
  justify-content: center;
  min-height: 100vh;
  padding: 40px 15px;
}

.pp-content {
  background-color: var(--white-alpha-25);
  -webkit-backdrop-filter: var(--backdrop-filter-blur);
          backdrop-filter: var(--backdrop-filter-blur);
  border-radius: 30px;
  border: 1px solid var(--white-alpha-40);
  max-width: 1200px;
  padding: 30px;
  width: 100%;
}
.pp-header {
  position: relative;
}

.pp-header .btn {
  align-items: center;
  display: flex;
  height: 40px;
  justify-content: center;
  padding: 0;
  position: absolute;
  right: -40px;
  top: -40px;
  width: 40px;
}

.pp-thumbnail {
  align-items: center;
  display: flex;
}

.pp-thumbnail img {
  border-radius: 10px;
  margin: 0 auto;
}

.pp-header h3 {
  font-size: 2.5rem;
  margin: 20px 0 15px;
  text-transform: capitalize;
}
.pp-body .description {
  margin-bottom: 20px;
}

.pp-body .general-info li {
  font-weight: 500;
  margin-bottom: 10px;
  text-transform: capitalize;
}

.pp-body .general-info li span {
  font-weight: 300;
}

.link-project {
  align-items: center;
  display: flex;
  flex-direction: column;
}
.link-project a {
  margin-top: 10px;
}

/* animacion */
.portfolio-popup {
  -webkit-animation: fadeIn;
          animation: fadeIn;
  -webkit-animation-duration: 3500ms;
          animation-duration: 3500ms;
}

:root {
  --main-color: #e02f6b;
  --blue: #0000ff;
  --blue-dark: #18293c;
  --orange: #ffa500;
  --green-yellow: #cddc39;
  --body-background: #f0f3fa;
  --pink-light: #efa2b4;
  --cyan-light: #aef1ee;
  --white: #fff;
  --white-alpha-40: rgba(255, 255, 255, 0.4);
  --white-alpha-25: rgba(255, 255, 255, 0.25);
  --backdrop-filter-blur: blur(5px);
  --numeros-letras-primario: #111521;
  --letras-cover: #f9f9f9;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  outline: none;
}

::before,
::after {
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  background-image: linear-gradient(
    to bottom right,
    #efa2b4,
    #aef1ee);
  background-image: linear-gradient(
    to bottom right,
    var(--pink-light),
    var(--cyan-light)
  );
  background-attachment: fixed;
  color: #18293c;
  color: var(--blue-dark);
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  font-weight: 300;
  line-height: 1.5;
  min-height: 100vh;
  overflow-x: hidden;
  padding: 35px 15px;
  -webkit-tap-highlight-color: transparent;
}

body::before {
  background-color: #cddc39;
  background-color: var(--green-yellow);
  content: "";
  height: 100%;
  left: 0;
  opacity: 0.12;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: -1;
}

a {
  text-decoration: none;
}

h1,
h2 {
  font-weight: 600;
}

h3,
h4,
h5,
h6 {
  font-weight: 500;
}

img {
  max-width: 100%;
  vertical-align: middle;
}

ul {
  list-style: none;
}

section {
  background-color: rgba(255, 255, 255, 0.25);
  background-color: var(--white-alpha-25);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  -webkit-backdrop-filter: var(--backdrop-filter-blur);
          backdrop-filter: var(--backdrop-filter-blur);
  border: 1px solid rgba(255, 255, 255, 0.4);
  border: 1px solid var(--white-alpha-40);
  border-radius: 30px;
  min-height: calc(100vh - 70px);
}

.main {
  max-width: 1200px;
  margin: auto;
  position: relative;
}

/* .container { */
/*   padding: 0 40px; */
/*   width: 100%; */
/* } */

.row {
  display: flex;
  flex-wrap: wrap;
}

.aling-items-center {
  align-items: center;
}

.flex-end {
  justify-content: flex-end;
}

/* Section title */

.section-title {
  padding: 0 15px;
  width: 100%;
  text-align: center;
}

.section-title h2 {
  font-size: 4rem;
  text-transform: capitalize;
}

/* Custom scrollbar */

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  background-color: #fff;
  background-color: var(--white);
}

::-webkit-scrollbar-thumb {
  background-color: #e02f6b;
  background-color: var(--main-color);
}

/* buttons */

button {
  font-family: inherit;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.btn {
  background-color: rgba(255, 255, 255, 0.25);
  background-color: var(--white-alpha-25);
  border: 1px solid rgba(255, 255, 255, 0.4);
  border: 1px solid var(--white-alpha-40);
  border-radius: 30px;
  color: #e02f6b;
  color: var(--main-color);
  cursor: pointer;
  display: inline-block;
  font-family: inherit;
  font-size: 1.6rem;
  font-weight: 500;
  line-height: 1.5;
  overflow: hidden;
  padding: 10px 28px;
  position: relative;
  text-transform: capitalize;
  transition: width 0.3s ease;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  vertical-align: middle;
}

.btn::before {
  background-color: #e02f6b;
  background-color: var(--main-color);
  content: "";
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  transition: width 0.3s ease;
  width: 0%;
  z-index: -1;
}

.btn:focus {
  color: #fff;
  color: var(--white);
}

.btn:focus::before,
.btn:hover::before {
  width: 100%;
}

.btn:hover {
  color: #fff;
  color: var(--white);
}

@media screen and (min-width: 720px) {
  .container {
    padding: 0 40px;
    width: 100%;
  }

  .sec-padding {
    padding: 80px 0;
  }

  .section-title {
    margin-top: 0;
    margin-bottom: 40px;
  }
  /* home */
  .home-section {
    display: flex;
    padding: 120px 0;
  }
  .home-text,
  .home-img {
    width: 50%;
  }
  .home-text h1 {
    font-size: 5rem;
  }
  /* about */
  .about-img {
    width: 40%;
  }

  .about-text {
    width: 60%;
  }

  .about-text .timeline-item {
    padding: 10px 0 0 40px;
  }
  /* portfolio */
  .portfolio-item {
    width: calc((100% / 3) - 30px);
  }
  .portfolio-section h2 {
    padding-top: 0;
    padding-bottom: 10px;
  }
  /* info */
  .contact-info {
    justify-content: space-evenly;
  }
}
@media (prefers-color-scheme: dark) {
  :root {
    --main-color: #e02f6b;
    --blue: #0000ff;
    --blue-dark: #18293c;
    --orange: #ffa500;
    --green-yellow: #cddc39;
    --body-background: #f0f3fa;
    --pink-light: #a88aeb;
    /* --pink-light: #efa2b4; */
    --cyan-light: #aef1ee;
    --white: #fff;
    --white-alpha-40: rgba(255, 255, 255, 0.4);
    --white-alpha-25: rgba(255, 255, 255, 0.25);
    --backdrop-filter-blur: blur(5px);
    --numeros-letras-primario: #111521;
    --letras-cover: #f9f9f9;
  }

  ::-webkit-scrollbar-track {
    background-color: #efa2b4;
    background-color: var(--pink-light);
  }

  ::-webkit-scrollbar-thumb {
    background-color: #18293c;
    background-color: var(--blue-dark);
  }
}

